import { PayloadAction } from '@reduxjs/toolkit';
import { createSlice } from 'utils/@reduxjs/toolkit';
import { useInjectReducer, useInjectSaga } from 'utils/redux-injectors';
import { AppProfilingSaga } from './saga';
import { AppProfilingState } from './types';

export const initialState: AppProfilingState = {
  loading: false,
  error: null,
  AppProfilingData: [],
};

const slice = createSlice({
  name: 'AppProfiling',
  initialState,
  reducers: {
    loadAppProfilingData: state => {
      state.loading = true;
    },
    AppProfilingDataLoaded: (state, action: PayloadAction<any>) => {
      state.loading = false;
      state.error = null;
      // console.log(action.payload)
      if (action.payload.length === 0) {
        state.AppProfilingData = [];
      } else {
        state.AppProfilingData.push(action.payload);
      }
    },
    AppProfilingDataError: (state, action: PayloadAction<any>) => {
      state.loading = false;
      state.error = action.payload;
      //  state.AppProfilingData = [];
    },
  },
});

export const { actions: AppProfilingActions } = slice;

export const useAppProfilingSlice = () => {
  useInjectReducer({ key: slice.name, reducer: slice.reducer });
  useInjectSaga({ key: slice.name, saga: AppProfilingSaga });
  return { actions: slice.actions };
};

