// @ts-nocheck
import { Flex, Img } from '@chakra-ui/react';
import ChartLayout from 'app/components/ChartLayout';

import React, { useEffect } from 'react';
import MobileDisplay from '../DisplayScreen/components/MobileDisplay';
import { useSelector, useDispatch } from 'react-redux';
import {
  selectAppProfilingData,
  selectLoading,
  selectError,
} from './slice/selectors';
import { AppProfilingErrorType } from './slice/types';
import { useAppProfilingSlice } from './slice';
import AppProfilingData from '../../../types/AppProfiling';
import { selectSessionId } from '../TestAnalytics/testAnalyticsSlice/selectors';
import Charts from './components/Charts';

import { selectTestSessionData } from '../TestSession/slice/selectors';
import LoadingComponent from 'app/components/LoadingComponent';
import SliderAxis from './components/SliderAxis';
import NoDataChart from './components/NoDataChart';
import { useParams } from 'react-router-dom';
import ChartKpi from './Features/ChartKpi';
import NoContent from 'app/components/NoContent';
const kpiList = ['cpu', 'battery', 'memory', 'graphics'];

export function AppProfiling() {
  //console.log(appProfilingKpiData);
  //let AppProfilingData: AppProfilingData;
  const { testId } = useParams();
  const { actions } = useAppProfilingSlice();
  const isLoading = useSelector(selectLoading);
  const error = useSelector(selectError);
  const dispatch = useDispatch();

  const AppProfilingData = useSelector(selectAppProfilingData);
  const testSessionData = useSelector(selectTestSessionData);

  const startTime =
    Object.keys(testSessionData).length !== 0 &&
    new Date(testSessionData.testStartTime).valueOf();
  const startTimeIST =
    Object.keys(testSessionData).length !== 0 && testSessionData.testStartTime;
  const endTimeIST =
    Object.keys(testSessionData).length !== 0 && testSessionData.testEndTime;
  const endTime =
    Object.keys(testSessionData).length !== 0 &&
    new Date(testSessionData.testEndTime).valueOf();
  //console.log(endTime , startTime);
  // const startTime =
  //   Object.keys(AppProfilingData).length !== 0 &&
  //   new Date(AppProfilingData[0].startTime).valueOf();
  // const endTime =
  //   Object.keys(AppProfilingData).length !== 0 &&
  //   new Date(AppProfilingData[0].endTime).valueOf();
  const duration = endTime - startTime;

  useEffect(() => {
    dispatch(actions.AppProfilingDataLoaded([]));
    for (let i = 0; i < kpiList.length; i++) {
      const name = kpiList[i];
      const params = { testId, name, i };
      dispatch(actions.loadAppProfilingData(params));
    }
  }, [testId]);

  //  AppProfilingData.length !==0 && AppProfilingData.sort(function(a,b){

  //   return(a.id - b.id)
  // })

  // const useEffectOnMount = (effect: React.EffectCallback) => {
  //   // eslint-disable-next-line react-hooks/exhaustive-deps
  //   useEffect(effect, [testId]);
  // };

  // useEffectOnMount(() => {
  //   dispatch(
  //     actions.loadAppProfilingData(testId)  );
  // });

  // const onSubmitForm = (evt?: React.FormEvent<HTMLFormElement>) => {
  //   /* istanbul ignore next  */
  //   if (evt !== undefined && evt.preventDefault) {
  //     evt.preventDefault();
  //   }
  // };

  return (
    <Flex width="100%">
      <Flex width="100%">
        {isLoading ? (
          <LoadingComponent></LoadingComponent>
        ) : (
          <SliderAxis
            startTime={startTimeIST}
            endTime={endTimeIST}
            duration={duration}
          >
            <Flex
              width="100%"
              overflowY="scroll"
              paddingTop="2px"
              //marginTop="10px"
              flexDirection="column"
              // background={height ? 'white5' : 'white'}
              height="443px"
              css={{
                '&::-webkit-scrollbar': {
                  width: '8px',
                  //height: '30px',
                  border: '5px solid #F3F6F9',
                },
                '&::-webkit-scrollbar-track': {
                  boxShadow: 'inset 0 0 6px white',
                },
                '&::-webkit-scrollbar-thumb': {
                  backgroundColor: '#C1C1C1',
                  borderRadius: '5px',
                  boreder: '2px solid',
                },
              }}
              bg="white"
            >
              {AppProfilingData.length > 0 ? (
                <>
                  {AppProfilingData.slice()
                    .sort((a, b) => a.id - b.id)
                    .map(kpiData => {
                      return (
                        <>
                          {' '}
                          {kpiData.data.length > 0 ? (
                            <Charts
                              kpiData={kpiData.data}
                              // isLoading={isLoading}
                              shadow={true}
                              title={kpiData.name}
                            ></Charts>
                          ) : (
                            <NoDataChart title={kpiData.name}></NoDataChart>
                          )}
                        </>
                      );
                    })}
                </>
              ) : (
                <NoContent message="No Content Available"></NoContent>
              )}
            </Flex>
          </SliderAxis>
        )}

        {/* <Flex
              width="100%"
              overflow="scroll"
              paddingTop="2px"
              //marginTop="10px"
              flexDirection="column"
              // background={height ? 'white5' : 'white'}
              height="467px"
              css={{
                '&::-webkit-scrollbar': {
                  width: '0px',
                },
              }}
              bg="white"
            >
           <ChartKpi  ></ChartKpi>
        <ChartKpi name="cpu"  ></ChartKpi>
        <ChartKpi name="memory"  ></ChartKpi>
     
      </Flex>  */}
      </Flex>
    </Flex>
  );
}

export default AppProfiling;

export const repoErrorText = (error: AppProfilingErrorType) => {
  switch (error) {
    case AppProfilingErrorType.RESPONSE_ERROR:
      return 'There is no such user ';
    default:
      return 'An error has occurred!';
  }
};
