// @ts-nocheck
import { call, put, takeLatest, delay, takeEvery } from 'redux-saga/effects';
import { request } from 'utils/request';
import { AppProfilingActions as actions } from '.';
import { AppProfilingData } from 'types/AppProfiling';
import { AppProfilingErrorType } from './types';
import GetIdToken from 'Authentication/configuration/GetIdToken';
import { REACT_APP_TEST_ANALYTICS_BASE_URL } from 'utils/enviroment';

const BASE_URL = REACT_APP_TEST_ANALYTICS_BASE_URL;

/**
 * Github repos request/response handler
 */
export function* getAppProfilingData(params) {
  const idToken = GetIdToken();
  //console.log(params.payload);
  // yield delay(1000);
  //  const requestURL = `${BASE_URL}/test-session`;
  const requestURL = `${BASE_URL}/tests/${params.payload.testId}/device/resource/${params.payload.name}`;
  const config = {
    method: 'GET',
    headers: {
      'Content-type': 'application/json',
      Authorization: `Bearer ${idToken}`,
    },
  };

  try {
    // Call our request helper (see 'utils/request')
    const AppProfilingData = yield call(request, requestURL, config);
    // console.log(AppProfilingData);
    if (Object.keys(AppProfilingData.body).length !== 0) {
      // console.log(AppProfilingData.body.columns);
      yield put(
        actions.AppProfilingDataLoaded({
          data: AppProfilingData.body.columns,
          name: params.payload.name,
          id: params.payload.i,
        }),
      );
    } else {
      yield put(
        actions.AppProfilingDataError(AppProfilingErrorType.RESPONSE_ERROR),
      );
    }
  } catch (err) {
    /*
    if (err.response?.status === 404) {
      yield put(actions.repoError(RepoErrorType.USER_NOT_FOUND));
    } else if (err.message === 'Failed to fetch') {
      yield put(actions.repoError(RepoErrorType.GITHUB_RATE_LIMIT));
    } else {
    */
    yield put(
      actions.AppProfilingDataError(AppProfilingErrorType.RESPONSE_ERROR),
    );
  }
}

/**
 * Root saga manages watcher lifecycle
 */
export function* AppProfilingSaga() {
  // Watches for loadRepos actions and calls getRepos when one comes in.
  // By using `takeLatest` only the result of the latest API call is applied.
  // It returns task descriptor (just like fork) so we can continue execution
  // It will be cancelled automatically on component unmount
  yield takeEvery(actions.loadAppProfilingData.type, getAppProfilingData);
}
